import React, { Component } from "react";
import './Menu.css';
import { NavLink, Link } from "react-router-dom";

const Menu =(props)=> {

    return(
        <div className="o-menu-panel">
            <div className="o-menu-content">
                <div className="o-menu-option">
                    <Link to="/Profile">
                        <img src={require('../Images/perfil.png')}/>
                        <p>Perfil</p>
                    </Link>
                </div>

                <div className="o-menu-option">
                    <Link to="/inicio">
                        <img src={require('../Images/inicio.png')}/>
                        <p>Inicio</p>
                    </Link>
                </div>

                <div className="o-menu-option">
                    <Link to="/NotFound">
                        <img src={require('../Images/pruebas.png')}/>
                        <p>Explorar pruebas</p>
                    </Link>
                </div>

                <div className="o-menu-option">
                    <Link to="/NotFound">
                        <img src={require('../Images/configuracion.png')}/>
                        <p>Configuracin</p>
                    </Link>
                </div>

                <div className="o-menu-option">
                    <Link to="/Notifications">
                        <img src={require('../Images/notificaciones.png')}/>
                        <p>Notificaciones</p>
                    </Link>
                </div>
            </div>

            <button className="o-exit-button" > <a href="/prototipo-ICFES/index.html"><i className="fas fa-sign-out-alt"></i>SALIR</a></button>
            
        </div>
    );
}

export default Menu;