import React, { Component } from 'react';
import { Col, Container, Row } from 'react-bootstrap';

import './Profile.css';

import Header from '../Components/Header';
import Menu from '../Components/Menu';
import CardCertificados from '../Components/CardCertificados';
import CardMas from '../Components/CardMas';
import CardCursos from '../Components/CardCursos';

class Profile extends Component {
    render() {

        const {headerInfo} = this.props;

        return(
            <Container fluid className="ContaiProfile">
                <Col className="ColMenuP">
                    <div className="o-principal-panel">
                        <div className="o-left-panel">
                            <div className="o-left-container">
                                <Header content={headerInfo} heigth={"15%"}></Header>
                                <div className="o-content-container">
                                    <div className="o-menu-container">
                                        <Menu></Menu>
                                    </div>
                                </div>
                            </div>                    
                        </div>
                    </div>
                </Col>

                <Col className="ColInfoPerfil">
                    <Row className="o-tittle-container">
                        <h2>PERFIL</h2>
                        <h1>Paula Corts</h1>
                    </Row>                    

                    <Row className="o-panel-info o-panel-start">
                        <p className="o-description">En esta seccin puedes ver tu historial y descargar certificados para tu hoja de vida</p>
                        <div className="DivEditProfile">
                            <h3>PERFIL</h3>
                            <i className="far fa-edit"></i>
                        </div>
                        <Col className="ColProfileDescrip">
                            <p>Tecnloga en anlisis y desarrollo de sistemas de informacin del SENA, con experiencia como asistente de informacin para empresas
                                del sector tecnolgico. Interesada en trabajar en desarrollo front-end remotamente.</p>
                        </Col>
                    </Row>

                    <Row className="RowCertificados">
                        <h3>CERTIFICADOS</h3>
                        <Row className="RowCardsCer">
                            <CardCertificados image={require('../Images/trabajoRemotoGreen.svg')} tittle="Trabajo remoto" fecha="21/05/2020" puntos="455"></CardCertificados>
                            <CardCertificados image={require('../Images/programacionGreen.svg')} tittle="Programacin CSS" fecha="12/07/2020" puntos="395"></CardCertificados>
                            <CardMas tittle="Explorar ms pruebas"></CardMas>
                        </Row>
                    </Row>

                    <Row className="RowPruebasCurso">
                        <h3>PRUEBAS EN CURSO</h3>
                        <Row className="RowCursos">
                            <CardCursos className="CardC" image={require('../Images/IconCardIlustracion.svg')} tittle="Ilustracin digital" color="#35BA99"></CardCursos>
                            <CardCursos image={require('../Images/IconCardProgramacion.svg')} tittle="Programacin en HTML" color="#7D7FBD"></CardCursos>
                        </Row>
                    </Row>
                </Col>
                
            </Container>
            
        );
    }
}

export default Profile;