import React, { Component } from 'react';
import { Col, Container, Row } from 'react-bootstrap';

import Header from '../Components/Header';
import Menu from '../Components/Menu';
import NotificationItem from '../Components/NotificationItem';

class Notifications extends Component {
    render() {

        const {headerInfo} = this.props;

        return(
            <Container fluid className="ContaiProfile">
                <Col className="ColMenuP">
                    <div className="o-principal-panel">
                        <div className="o-left-panel">
                            <div className="o-left-container">
                                <Header content={headerInfo} heigth={"15%"}></Header>
                                <div className="o-content-container">
                                    <div className="o-menu-container">
                                        <Menu></Menu>
                                    </div>
                                </div>
                            </div>                    
                        </div>
                    </div>
                </Col>

                <Col className="ColInfoPerfil">
                    <Row className="o-tittle-container">
                        <h2>NOTIFICACIONES</h2>
                        <h1> <span>Hola,</span>  Paula</h1>
                        <p className="o-description">Tienes nuevos mensajes.</p>
                    </Row>                    

                    <Row className="o-panel-info o-panel-start">
                        <NotificationItem 
                            text="Has acumulado 50 Puntos Saber, puedes reclamar un bono de descuento del 10% para tu prximo certificado"
                        />
                        <NotificationItem 
                            text="Completaste un nivel, has acumulado 15 nuevos Puntos Saber"
                        />
                        <NotificationItem 
                            text="Empieza a certificar una nueva habilidad y gana ms Puntos Saber"
                        />
                    </Row>
                    
                </Col>
                
            </Container>
            
        );
    }
}

export default Notifications;