import React from 'react';

import './CardCursos.css';

const CardCursos = (props)=> {
  const {tittle, image, color} = props;
  return (  
      <div className="o-card-cursos">
            <div className="o-box-img-curso" style={{backgroundColor:color}}>
                <img src={image}/>
            </div>

            <p>{tittle}</p>
      </div> 
    );
}

export default CardCursos;