import React from 'react';
import './CardSubscribe.css';
import './Button.css';
import './CardCertificados.css';

import BoxInfo from './BoxInfo';
import { Link } from 'react-router-dom';

const CardMas = (props)=> {
  const {tittle, image} = props;
  return (  
      <div className="o-card-subscribe">
            <BoxInfo toggle={false} image={image} tittle={tittle} color="white"></BoxInfo> 
            <br></br><br></br><br></br><br></br>
            <Link to="/NotFound"><button className="o-button-secundary o-button-subscribe">VER MS</button></Link>
      </div> 
  );
}

  export default CardMas;