import React, {Component} from 'react';
import './App.css';
import {
  //BrowserRouter as Router,
  Route,
  Switch,
  HashRouter as Router
} from "react-router-dom";

import Home from './Views/Home';
import Login from './Views/Login';
import Register from './Views/Register';
import Camera from './Views/Camera';
import Start from './Views/Start';
import Ranking from './Views/Ranking';
import LevelPresentation from './Views/LevelPresentation';
import QuestionsList from './Views/QuestionsList';
import FinalLevel from './Views/FinalLevel';
import LoginEmpleadores from './Views/Login_Empleadores';
import NextLevel from './Views/NextLevel';
import Certificar from './Views/Certificar';
import NotFound from './Views/NotFound';
import Profile from './Views/Profile';
import PDFviewer from './Views/Certificados/PDFviewer';
import Notifications from './Views/Notifications';
import Empleadores from './Views/Empleadores';

class App extends Component {

  constructor(){
    super(); 
    this.state={

    }
  }

  render(){
    return (
      <Router>
        <div className="App">     
          <Switch>
              <Route exact path="/" component={Home}/>
              <Route exact path="/ingreso" component={Login}/>
              <Route exact path="/ingresoEmpleadores" component={LoginEmpleadores}/>
              <Route exact path="/registro" component={Register}/>
              <Route exact path="/camara" component={Camera}/>
              <Route exact path="/inicio" component={Start}/>
              <Route exact path="/ranking" component={Ranking}/>
              <Route exact path="/nivel" component={LevelPresentation}/>
              <Route exact path="/Nextnivel" component={NextLevel}/>
              <Route exact path="/certificar" component={Certificar}/>
              <Route exact path="/pregunta" component={QuestionsList}/>
              <Route exact path="/pregunta-final" component={FinalLevel}/>
              <Route exact path="/NotFound" component={NotFound}/>
              <Route exact path="/Profile" component={Profile}/>
              <Route exact path="/PDFviewer" component={PDFviewer}/>
              <Route exact path="/Notifications" component={Notifications}/>
              <Route exact path="/empleadores" component={Empleadores}/>
          </Switch>
        </div>
    </Router>
      
    );
  }
  
}

export default App;