import React, { Component } from 'react';
import './PDFviewer.css';

class PDFviewer extends Component{

    componentDidMount() {
        if (typeof window.orientation !== "undefined"){
            document.getElementById('EnlaceDescargaPDF').click();
            window.close();
        }
    }

    render() {
        return(
            <div className="divViewer">
                <object
                    data={require('../Certificados/PDFs/Certificado.pdf')}
                    type="application/pdf"
                    width="100%"
                    height="100%">
                            <p>Se descargar la historia clnica en un momento. De lo contrario, haga clic 
                                <a href={require('../Certificados/PDFs/Certificado.pdf')} id="EnlaceDescargaPDF" download="Certificado.pdf">aqu</a>
                            </p>
                </object>
            </div>
        );
    }
}

export default PDFviewer;