import React, { Component } from "react";
import './Login.css';
import ButtonPrimary from '../Components/ButtonPrimary'
import { Link } from "react-router-dom";


class Register extends Component{
    constructor(props) {
        super(props);
        this.state = {
            
        };
    }

    register(evt){
        evt.preventDefault();
        //localStorage.setItem("emotion", radioValue);
        this.props.history.push('/camara');
    }

    render() {
        return(
            <div className='o-login-container'>                    
                <Link to="/">
                    <div className="o-back-button">
                        <i className="fas fa-chevron-left"></i>
                        <h2>ATRS</h2>               
                    </div>
                </Link>

                <div className="o-login-card">
                    <img src={require('../Images/logo.png')} alt="Logo" className="o-logo-login"/>
                    <form onSubmit={this.register.bind(this)} className="o-form-login" >
                        <select required>
                            <option value="" selected>Tipo de documento</option>
                            <option value="TI">Tarjeta de identidad</option>
                            <option value="CC">Cdula de ciudadana</option>
                            <option value="CE">Cdula de extranjera</option>
                            <option value="CR">Contrasea registradura</option>
                            <option value="PC">Pasaporte colombiano</option>
                            <option value="PE">Pasaporte extranjero</option>
                            <option value="PP">Permiso especial de permanencia</option>
                            <option value="NESC">Nmero establecido por la secretara de educacin</option>
                            <option value="CCa">Certificado de cabildo</option>
                            <option value="NUIP">Nmero nico de identificacin personal</option>
                            <option value="NIP">Nmero de identificacin personal</option>
                            <option value="RI">Registro civil</option>

                            <img src={require('../Images/flecha.png')}/>
                        </select>
                        <input type='number' placeholder="Nmero de documento" name="numero-documento" required/>
                        <input type='text' placeholder="Nombres" name="nombres" required/>
                        <input type='text' placeholder="Apellidos" name="apelidos" required/>
                        <input type='password' placeholder="Contrasea" autoComplete="new-password" name="contrasea" required/>
                        <input type='password' placeholder="Confirmar Contrasea" autoComplete="new-password" name="confirmar-contrasea" required/>
                        <ButtonPrimary type='submit' text="INGRESAR" margin="1em 10em" color="green_button" padding="0.8rem 0"></ButtonPrimary>
                        
                    </form>
                </div>
            </div>
        );
    }

}

export default Register;