import React, { Component } from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import CardSubscribe from '../Components/CardSubscribe';
import ItemRanking from '../Components/ItemRanking';
import { NavLink, Link } from "react-router-dom";
import ButtonPrimary from '../Components/ButtonPrimary';

import './Empleadores.css';

import Checkbox from '@material-ui/core/Checkbox';
import { FormControlLabel, FormGroup } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';

class Empleadores extends Component {
    render() {

        const PurpleCheckbox = withStyles({
            root: {
              color: '#86888A',
              '&$checked': {
                color: '#725FA9',
              },
              
            },
            checked: {},
        })
        
        ((props) => <Checkbox color="default" {...props} />);

        return(
            <div className="AllEmple">
                <Container fluid className="ContaiEmpleadores">
                    
                    <Row className="RowMenuBar">
                        <img src={require('../Images/logo.svg')}></img>

                        <div className="divMenuOptions">
                            <ul className="o-options-header-home">
                                <NavLink activeClassName="active" className="o-option-home" exact to="/NotFound">
                                    <li>El Icfes</li>
                                </NavLink>
                                <span> | </span>
                                <NavLink activeClassName="active" className="o-option-home" to="/ingresoEmpleadores">
                                    <li>Empleadores</li>
                                </NavLink>
                                <span> | </span>
                            </ul>
                            
                            <ButtonPrimary text="INICIO" color="green_button"></ButtonPrimary>
                        </div>                        
                    </Row>

                    <Row className="RowBusqueda">
                        <h1>Encuentra el talento que has estado buscando</h1>

                        <FormGroup row className="CheckboxGroup">
                            <FormControlLabel control={<PurpleCheckbox/>} label="Buscar por nombre"></FormControlLabel>
                            <FormControlLabel control={<PurpleCheckbox/>} label="Buscar por nombre"></FormControlLabel>
                            <FormControlLabel control={<PurpleCheckbox/>} label="Buscar por nombre"></FormControlLabel>
                        </FormGroup>

                        <div className="o-search-container">
                            <button type="submit"></button>
                            <input type="text" name="Buscar"/>
                        </div>     

                        <div className="divBSearch">
                            <ButtonPrimary text="BUSCAR" color="blue_button"></ButtonPrimary>
                        </div>
                    </Row>

                    <Row className="RowRankingCertificados">
                        <h3>DESCUBRE LAS PERSONAS MS DESTACADAS EN CADA CERTIFICACIN</h3>
                        <p>Consulta nuestro ranking por certificado de los usuarios buscando nuevas oportunidades laborales.</p>
                        
                        <Row className="RowRankCards">
                            <CardSubscribe image={require('../Images/trabajoRemotoGreen.svg')} tittle="Trabajo remoto" text="VER MS" certificates="200"></CardSubscribe>
                            <CardSubscribe image={require('../Images/trabajoRemotoGreen.svg')} tittle="Servicio al cliente" text="VER MS" certificates="150"></CardSubscribe>
                            <CardSubscribe image={require('../Images/trabajoRemotoGreen.svg')} tittle="Programacin en HTML" text="VER MS" certificates="100"></CardSubscribe>
                        </Row>
                    </Row>

                    <Row className="RowRankC">
                        <h3><span>Ranking de</span> trabajo remoto</h3>
                        <ItemRanking number="2" certificates="18" points="1743"></ItemRanking>
                        <ItemRanking number="2" certificates="18" points="1743"></ItemRanking>
                        <ItemRanking number="2" certificates="18" points="1743"></ItemRanking>
                        <ItemRanking number="2" certificates="18" points="1743"></ItemRanking>
                        <ItemRanking number="2" certificates="18" points="1743"></ItemRanking>
                        <ItemRanking number="2" certificates="18" points="1743"></ItemRanking>
                        <ItemRanking number="2" certificates="18" points="1743"></ItemRanking>
                    </Row>
                </Container>                
            </div>
        );
    }
}

export default Empleadores;