import React from 'react';
import './CardSubscribe.css';
import './Button.css';
import './CardCertificados.css';

import BoxInfo from './BoxInfo';
import { Link } from 'react-router-dom';

const CardCertificados = (props)=> {
  const {tittle, image, fecha, puntos} = props;
  return (  
      <div className="o-card-subscribe">
            <BoxInfo toggle={false} image={image} tittle={tittle} color="white"></BoxInfo>
            <Link to="/PDFviewer" target="_blank"><button className="o-button-secundary o-button-subscribe">DESCARGAR CERTIFICADO</button></Link>
            <div className="divShare">
                <p>Compartir</p>
                <i className="far fa-share-square"></i>
            </div>
            <div className="o-line-subscribe"></div>
            <div className="o-certificates-label-container2">
                <p>{'Fecha: ' + fecha}</p>
                <p>{'Puntaje: ' + puntos + ' puntos'}</p>
            </div>
      </div> 
  );
}

  export default CardCertificados;